/* style for product */
.product {
  width: 100%;
  height: 100%;
}

.product > div {
  width: inherit;
}

/* style for product title */
.product-title {
  display: flex;
  flex-direction: column;
  gap: 0.7rem;
  justify-content: flex-start;
  align-items: flex-start;
}

.product-title p {
  font-size: 1.6rem;
  color: #81797d;
}

.product-title input[type="text"] {
  border: 1px solid #e4e4e5;
  width: 30rem;
  line-height: 2rem;
  padding: 0.5rem;
  font-size: 1.4rem;
}

.product-title input[type="text"]:focus {
  outline: none;
}

.product-title button {
  font-size: 1.4rem;
}

.product-title__action {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

/* style for product list */

.product-list p {
  color: #81797d;
}

.product-list > p {
  font-size: 1.6rem;
}

/* styles for table of products */
.product-table {
  margin-top: 1rem;
  width: 100%;
  font-size: 1.2rem;
  /* border-collapse: collapse; */
  border-spacing: 0;
  /* border: 1px solid #81797d; */
  border: 1px solid #f4f6f9;
  border-radius: 5px;
}

.product-table tr td {
  padding: 1rem 0.3rem 1rem 0.5rem;
  /* border-bottom: 1px solid #81797d; */
  border: 1px solid #eaebec;
}

tr.tr-even td {
  background-color: #f2f6f8;
}

/* .product-table tbody tr:last-child td {
  border: none;
} */

.product-table thead tr td > div {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;

  p {
    vertical-align: middle;
    display: inline-block;
    font-weight: 500;
  }
}

.product-table tr td button {
  font-size: 1.2rem;
}

p.colorBlack {
  color: black;
}

input[type="checkbox"] {
  vertical-align: middle;
}
