* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  font-size: 2.4rem;
}

/* Style chung cho button */
.btn {
  padding: 1.5rem 1rem;
  background-color: #0071c2;
  border: none;
  cursor: pointer;
  color: #f3f4f5;
}

.btn-radius {
  border-radius: 0.5rem;
}

.btn-small {
  padding: 0.5rem;
  font-size: inherit;
  border-radius: 0.3rem;
}

.btn-green {
  background-color: #4e9c3f;
  color: #f3f4f5;
  border: 1px solid transparent;
}

.btn-green:hover {
  background-color: transparent;
  color: #4e9c3f;
  border: 1px solid #4e9c3f;
}

.btn-red {
  background-color: #ed5271;
  color: #f3f4f5;
  border: 1px dashed transparent;
}

.btn-red:hover {
  background-color: transparent;
  color: #ed5271;
  border: 1px dashed #ed5271;
}

/* style for root layout */
.root {
  width: 100%;
  height: 100vh;

  display: flex;
  flex-wrap: wrap;
}

/* style for root head */
.root-head {
  width: 100%;
  border-bottom: 1px solid #e9dbdb;
}

.root-head div {
  padding: 1rem;
  width: 20rem;
  border-right: 1px solid #e9dbdb;

  display: flex;
  justify-content: center;
  align-items: center;
}

.root-head h1 {
  color: #6e51ff;
}

/* style for root aside */
.root-aside {
  width: 20rem;
  min-height: calc(100% - 52.67px);
  border-right: 1px solid #e9dbdb;
  overflow-y: auto;
}

/* style for root main */
.root-main {
  flex: 1; /* Make the main content take up the remaining space */
  padding: 1rem;
  overflow-y: auto;
}

p.root-message {
  width: 100%;
  text-align: center;
  color: #0071c2;
  font-size: 1.6rem;
}

.error {
  color: red;
}
