/* style for dashboard */
.dashboard {
  width: 100%;
  height: 100%;
}

.dashboard h4 {
  color: #bdc0c3;
  margin-bottom: 3rem;
  font-size: 1.1rem;
}

/* style for info board */
.infoboard {
  width: 100%;

  display: flex;
  justify-content: space-between;
  /* align-items: center; */
  gap: 1rem;
  flex-wrap: wrap;
}

.infoboard > div {
  flex: 1;
}

/* style for item in board */
.itemboard {
  padding: 1rem;

  display: flex;
  align-items: center;
  justify-content: space-between;
}

.itemboard .info {
  display: flex;
  align-items: center;
}

.itemboard h4 {
  color: #bdc0c3;
  margin-bottom: 1rem;
}

.itemboard p {
  font-size: 1.6rem;
  font-weight: 500;
  padding: 0;
}

/* style for icon */
.icon {
  display: flex;
  justify-content: flex-end;
}

.icon i {
  font-size: 1.4rem;
  color: #a6aeb9;
}

.icon p {
  padding: 0.5rem;
  border-radius: 0.3rem;

  display: flex;
  justify-content: center;
  align-items: center;
}

.icon-red p {
  background-color: #ffcccc;
  color: #e7506b;
}

.icon-yellow p {
  background-color: #f8edd2;
  color: #e1b84f;
}

.icon-green p {
  background-color: #cce6cc;
  color: #5faf5f;
}

.icon-purple p {
  background-color: #e6cce6;
  color: #a245a2;
}

/* styles for transaction list */
.transaction {
  margin-top: 2rem;
}

.transaction-list {
  padding: 2rem;
}

.transaction-list p {
  color: #81797d;
}

.transaction-list > p {
  font-size: 1.6rem;
}

/* styles for table of transactions */
.trans-table {
  margin-top: 1rem;
  width: 100%;
  font-size: 1.2rem;
  /* border-collapse: collapse; */
  border-spacing: 0;
  /* border: 1px solid #81797d; */
  border: 1px solid #f4f6f9;
  border-radius: 5px;
}

.trans-table tr td {
  padding: 1rem 0 1rem 0.5rem;
  /* border-bottom: 1px solid #81797d; */
  border: 1px solid #eaebec;
}

tr.tr-even td {
  background-color: #f2f6f8;
}

.trans-table thead tr td > div {
  width: 100%;

  p {
    vertical-align: middle;
    display: inline-block;
    font-weight: 500;
  }
}

p.colorBlack {
  color: black;
}

input[type="checkbox"] {
  vertical-align: middle;
}

/* style for cols status */
p.status {
  border-radius: 3px;
  padding: 0.5rem;
  color: #468639;
  font-size: 1.4rem;
  width: fit-content;
}

p.booked {
  background-color: #f69685;
}

p.checkin {
  background-color: #39d139;
}

p.checkout {
  background-color: #d3d0f2;
}

/* style for last row */
tr.row-space td {
  height: 5rem;
}

.row-pages {
  width: 100%;

  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.row-pages p {
  padding: 1rem;
}

.pButton {
  cursor: pointer;
}

.pButton:hover {
  color: black;
}
