.form {
  margin: 4rem auto;
  width: 25rem;
  color: black;
}

.form-control {
  font-size: 1.2rem;
}

.form-control,
.form-actions {
  padding: 0 1rem 1rem 1rem;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.form-control input {
  width: 100%;
  line-height: 2.5rem;
  height: 5rem;
  padding: 0 0.5rem;
  border: 1px solid #a0a3a7;
  border-radius: 2px;
}

.form-control input:focus {
  outline: none;
  background-color: #e8f0fe;
}

.form-actions {
  flex-direction: column;

  .p_mess {
    font-size: 1.2rem;
  }
}

.form-actions button {
  width: 100%;
  border-radius: 5px;
  padding: 0.5rem 1rem;
  font-size: 1.4rem;
}
