/* style for nav bar */
.nav {
  padding: 1rem;

  color: #81797d;
}

.nav-list {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.nav-list li {
  display: flex;
  align-items: center;
  gap: 0.8rem;
}

.nav-list li p {
  font-size: 1.2rem;
  margin-top: 1.5rem;
}

/* style for icon */
.nav-list li i {
  margin-top: 0.3rem;
  width: 1.5rem;
  color: #6e51ff;
  font-size: 1.1rem;
  text-align: center;
}

/* style for link */
.nav-list li a {
  color: inherit;
  text-decoration: none;
  font-size: 1.3rem;

  display: flex;
  justify-content: center;
  align-items: center;
}

.nav-list li a:hover,
.nav-list li a:active,
.nav-list li a.active {
  color: #6e51ff;
}
