/* style for chat */
.chat {
  width: 100%;
  height: 100%;

  font-size: 1.2rem;
}

/* style for chat title */
.chat-title {
  height: 5rem;
}

/* style for chat contents */
.chat-contents {
  height: calc(100% - 5rem);
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.3);
  border-radius: 0.5rem;
}

.chat-contents__inner {
  height: 100%;

  display: flex;
  flex-direction: row;
}

.chat-list {
  width: 35%;
  height: 100%;
  border-right: 1px solid #eaebec;
}

.chat-list ul {
  list-style-type: none;
  width: 100%;
}

.chat-list ul li {
  width: 100%;
  padding: 2rem;

  border-bottom: 1px solid #eaebec;
}

.chat-list ul li.chat-list__item {
  padding: 1rem;

  display: flex;
  align-items: center;
  justify-content: space-evenly;
  gap: 0.5rem;
}

li.chat-list__item p {
  max-width: 80%;
  word-wrap: break-word;
}

/* style for chat content */
.chat-content {
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.chat-action {
  width: 100%;
  height: 5rem;
  border-top: #eaebec 1px solid;
  padding: 0 2rem;

  display: flex;
  justify-content: space-between;
  align-items: center;
}

.chat-action input[type="text"] {
  max-width: 90%;
}

/* style for message */

.chat-message {
  width: 100%;
  height: calc(100% - 5rem);
  max-height: 40rem;
  /* padding: 1rem 14.79rem 1rem 1rem; */
  padding: 1rem;
  overflow: auto;

  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}

.user-content {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}

.user-content p {
  background-color: #e8eff9;
  color: rgba(0, 0, 0, 0.8);
  padding: 1rem;
  border-radius: 0.3rem;
  max-width: 80%;
}

.guest-content {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 2rem;
}

.guest-content p {
  background-color: #d1f7f2;
  color: rgba(0, 0, 0, 0.4);
  padding: 1rem;
  border-radius: 0.3rem;
  max-width: 80%;
}

/* style for icon user */
.user-img {
  width: 4rem;
  border: none;
  background-color: transparent;
}

.user-img img {
  width: 100%;
  border-radius: 50%;
}

.color-blue {
  color: white;
  background-color: #48b0f7;
  padding: 0.5rem;
  border-radius: 50%;
  cursor: pointer;
}

/* style for input */
input[type="text"] {
  width: 100%;
  border: 1px solid #e4e4e5;
  line-height: 2rem;
  padding: 0.5rem;
  font-size: 1.4rem;
}

input[type="text"]:focus {
  outline: none;
}

h4 {
  font-size: 1.4rem;
}
