/* style for title */
.title {
  padding: 1rem 1rem;
}

.title h2 {
  color: rgba(0, 0, 0, 0.2);
  font-size: 1.4rem;
}

/* style for form */
.form {
  margin-top: 1rem;
  padding: 1rem 3rem;
}

/* style for fomr-controls */
.form-controls {
  margin-bottom: 2rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.form-controls .form-control {
  width: 45%;
}

.form-control {
  margin-bottom: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

/* style for element of form-control */

.form-control label {
  font-weight: bold;
  font-size: 1.2rem;
  color: #54545a;
}

.form-control input[type="text"],
.form-control input[type="number"] {
  border: 1px solid black;
  line-height: 2.5rem;
  padding-left: 1rem;
}

.form-control select {
  padding: 0.5rem;
}

.form-control textarea {
  resize: none;
}

.form-control input:focus,
textarea:focus,
select:focus {
  outline: none;
}

/* style for form-actions */
.form-actions {
  margin-top: 2rem;
}

.form-actions .button {
  padding: 0.8rem 4rem;
  cursor: pointer;
  background-color: #008080;
  border: 1px solid #008080;
  color: #e8ebeb;
}

.form-actions .button:hover,
.form-actions .button:focus {
  background-color: #fff;
  color: #008080;
  border: 1px solid #008080;
}

.form-actions .button:focus {
  outline: none;
}

/* style for list of room type */
.room-type__list {
  width: 100%;
  margin-bottom: 2rem;

  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  /* justify-content: space-between; */
  align-items: stretch;
}

.room-type__list .room-type__item {
  width: 30%;
}

.room-type__item label {
  display: flex;
  align-items: center;
  gap: 0.3rem;
}

@media (max-width: 818px) {
  .room-type__list .room-type__item {
    width: 45%;
  }
}

@media (max-width: 599px) {
  .room-type__list .room-type__item {
    width: 100%;
  }
}
